.add{
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}
.input{
  height: 44px;
  flex:1;
  box-sizing: border-box;
  border-radius: 10px 0 0 10px;
  padding: 12px 16px;
  box-shadow: none;
  outline:none;
  border: 2px solid #ccc;
  font-size: 16px;
}
.input:focus{
  border: 2px solid #4e6ef2;
}
.btn{
  width: 108px;
  height: 44px;
  background: #4e6ef2;
  color: white;
  font-size: 17px;
  border-radius: 0 10px 10px 0;
  font-weight: 400;
  border: none;
}
.btn:hover{
  cursor: pointer;
  background: #4662D9;
}